html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-barlow);
  line-height: 1.625rem;
  background: var(--background);
  color: var(--light-blue);
  min-height: 100vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--dark-blue);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
  font-weight: 900;
  line-height: 3.5rem;
  text-transform: uppercase;
}

h2 {
  font-size: 2rem;
  font-weight: 900;
  line-height: 2.25rem;
  text-transform: uppercase;
}

h3 {
  font-size: 1.5rem;
  font-weight: 900;
  line-height: 1.75rem;
  text-transform: uppercase;
}

:root {
  --dark-orange: hsl(22, 89%, 52%);
  --light-orange: hsl(22, 100%, 69%);
  --dark-blue: hsl(220, 50%, 17%);
  --light-blue: hsl(221, 23%, 58%);
  --grey: hsl(198, 28%, 93%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-barlow: "Barlow", sans-serif;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3.4375rem;
  margin-bottom: 5.1875rem;
  max-height: 3.625rem;
}
.header img {
  height: 2.5rem;
}
.button {
  background: var(--grey);
  padding: 1rem 1.6875rem;
  border-radius: 0.5rem;
}
.button__text {
  font-size: 1rem;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: var(--dark-blue);
}

.top {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}
.top__left {
  max-width: 27.8125rem;
  max-height: 19.5rem;
}
.top__left--title {
  padding-top: 3.75rem;
}
.top__left--description {
  margin-bottom: 2.5rem;
}
.top__left--wrapper {
  display: flex;
  align-items: center;
  max-height: 3.625rem;
}
.top__left--release {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 2.5rem;
}
.top__right {
  left: 40%;
}
.top__right img {
  max-width: 33.75rem;
  max-height: 30rem;
  background-size: cover;
  border-radius: 0.9375rem;
}